// Queensland Government Desing System: Cards
// Source: https://getbootstrap.com/docs/5.2/components/card/#variables

// Source CSS variables: var(--variable-name)  /node_modules/bootstrap/scss/components/_card.scss   

// Override as required to suit Design System
@include color-mode(dark) {
    .card {
        --#{$prefix}card-bg: var(--qld-white);
        --#{$prefix}card-color: var(--qld-text-grey);
    }
}

.card {
    
    --#{$prefix}card-bg: var(--qld-white);
    --#{$prefix}card-color: var(--qld-text-grey);
    
    --#{$prefix}card-spacer-x: #{$card-spacer-x};
    --#{$prefix}card-spacer-y: #{$card-spacer-y};
    --#{$prefix}card-title-spacer-y: #{$card-title-spacer-y};
    --#{$prefix}card-border-width: #{$card-border-width};
    --#{$prefix}card-border-color: #{$card-border-color};
    --#{$prefix}card-border-radius: #{$card-border-radius};
    --#{$prefix}card-box-shadow: #{$card-box-shadow};
    --#{$prefix}card-inner-border-radius: #{$card-inner-border-radius};
    --#{$prefix}card-cap-padding-y: #{$card-cap-padding-y};
    --#{$prefix}card-cap-padding-x: #{$card-cap-padding-x};
    --#{$prefix}card-cap-bg: #{$card-cap-bg};
    --#{$prefix}card-cap-color: #{$card-cap-color};
    --#{$prefix}card-height: #{$card-height};
    --#{$prefix}card-color: #{$card-color};
    --#{$prefix}card-img-overlay-padding: #{$card-img-overlay-padding};
    --#{$prefix}card-group-margin: #{$card-group-margin};

    --#{$prefix}card-border-color: #{$qld-light-grey};
    --#{$prefix}card-link-color: #{$link-color};
    --#{$prefix}card-footer-border: #{$qld-light-grey};
    --#{$prefix}card-border-radius: 0.75rem;
    

    //Variants
    &.card-alternative {
        --#{$prefix}card-title-color: #{$headings-color};
        --#{$prefix}card-bg: #{$qld-light-grey};
        --#{$prefix}card-color: #{$body-color};
        --#{$prefix}card-border-color: #7a7a7a;
        --#{$prefix}card-footer-border: #7a7a7a;
    }

    &.card-primary {
        --#{$prefix}card-title-color: #fff;
        --#{$prefix}card-bg: #{$primary};
        --#{$prefix}card-color: #fff;
        --#{$prefix}card-link-color: #fff;
        --#{$prefix}card-border-color: #2573c1;
        --#{$prefix}card-footer-border: #ffffff30; //white at 30%
    }
    &.card-primary--dark {
        --#{$prefix}card-title-color: #fff;
        --#{$prefix}card-bg: #{$primary-dark};
        --#{$prefix}card-color: #fff;
        --#{$prefix}card-link-color: #fff;
        --#{$prefix}card-footer-border: #ffffff30; //white at 30%
    }

    .card-title {
        line-height: 2rem;
        font-size: 1.5rem;
        margin-top: 0;
        margin-bottom: 0.75rem;

        a {
            color: var(--#{$prefix}card-link-color);
            text-decoration: none;

            &:hover {
                text-decoration: underline;
                text-underline-offset: 3px;
            }
        }
    }
    .card-img-top {
        border-top-left-radius: var(--qld-card-border-radius);
        border-top-right-radius: var(--qld-card-border-radius);
        border-bottom-right-radius: 1.875rem;
    }

    .card-footer {
        color: var(--#{$prefix}card-color);
        font-weight: 600;
        font-size: 1.25rem;
        line-height: 1.5;
        background: none;
        border-top: 0;
        position: relative;
        padding: var(--#{$prefix}card-spacer-y) var(--#{$prefix}card-spacer-x);
        padding-top: 2.5rem;

        &:before {
            content: "";
            position: absolute;
            left: var(--#{$prefix}card-spacer-x);
            right: var(--#{$prefix}card-spacer-x);
            top: calc(1.25rem - 1px);
            height: 1px;
            background: var(--#{$prefix}card-footer-border);
        }
    }

    small {
        font-size: 0.875rem;
    }
}
